
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
directives: {
TransferDom
},
name: '',
data() {
return {
swiperIndex: 0,
showToUpdate: true,
show8: false,
isShowNav: false,
drawerVisibility: false
}
},
components: {
XHeader,
Swiper,
SwiperItem,
Popup
},
computed: mapGetters([
'swiperList',
'scrollingMessageList'
]),
created() {

this.initSwiperList()
},
methods: {

demo06_onIndexChange() {
},

initSwiperList() {
let options = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
}
this.$store.dispatch('initSwiperList', options)
},

goSetting(){
this.$router.push({name: 'SettingPage'})
},

goLogin(){
this.$router.push({name: 'Login'})
}
},
mounted() {
let s = document.querySelector('.vux-header-left');
s.onclick = () => {
this.show8 = true;
}
}

}



